        right: auto;
    }
    96% {
        width: 0%;
        left: 0;
        right: auto;
    }
    100% {
        width: 0%;
        left: 0;
        right: auto;
        opacity: 0.3;
    }
}

.bx--text-truncate--end {
    width: 100%;
    display: inline-block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.bx--text-truncate--front {
    width: 100%;
    display: inline-block;
    direction: rtl;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

